import React from "react";
import "../css/sitemaps.css";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";

const Sitemaps = () => {
  return (
    <div className="content">
      <div class="containersitemaps">
        <Container>
          <Card className="card my-5 mx-3 p-3">
            <Card.Body>
              <Card.Title>
                <h1 className="m-5"></h1>
                <hr></hr>
                <br></br>
              </Card.Title>

              <Card.Text>
                <ul>
                  <li className="mb-2">
                    <a href="/"> Home</a>
                  </li>
                  <li className="mb-2">
                    <a href="/aboutpage"> About s</a>
                  </li>
                  <li className="mb-2">
                    <a href="#"> Products and Service</a>
                    <ul>
                      <li className="mb-2">
                        <a href="#"> Program Control</a>
                      </li>
                      <li className="mb-2">
                        <a href="#"> Machine</a>
                      </li>
                      <li className="mb-2">
                        <a href="#"> Chiller</a>
                      </li>
                      <li className="mb-2">
                        <a href="#"> Conveyor</a>
                      </li>
                      <li className="mb-2">
                        <a href="#"> Boiler</a>
                      </li>
                      <li className="mb-2">
                        <a href="#"> Heavy Rack</a>
                      </li>
                    </ul>
                  </li>
                  <li className="mb-2">
                    <a href="/reference"> Reference</a>
                  </li>
                  <li className="mb-2">
                    <a href="/contactpage"> Contact</a>
                  </li>
                  <li className="mb-2">
                    <a href="/sitemaps"> Sitemap</a>
                  </li>
                </ul>
              </Card.Text>
            </Card.Body>
          </Card>
        </Container>
      </div>
    </div>
  );
};

export default Sitemaps;
